

.App{
  background-color: rgb(27, 3, 3);
  color: aliceblue;
  width: 50%;
  border-radius: 20px;
  margin: 5em auto;
  position: relative;
  .header{
    background-color: beige;
    color: blue;
    margin: 0 auto;
    padding: 0.3em;
    width: 70%;
    border-radius: 5px;
    text-align: center;
    
  }
  .section2{
    padding: 0 2em;
    padding-bottom: 2em;
    
    .time{
      text-align: center;
      span{
        font-size: 1.5em;
        font-weight: bolder;
        text-transform: capitalize;
      }
      .flex{
      
      }
      input{
        margin: 0 1em;
        color: black;
      }
      .start{
        display: flex;
        justify-content: space-around;
        padding: 1em;
        border: 2px solid;
        margin: 1em;
      }
      .to{
        display: flex;
        justify-content: space-around;
        padding: 1em;
        border: 2px solid;
        margin: 1em;
      }
    }
    .descreption{
      padding: 2em;
      padding: 1em;
      border: 2px solid;
      margin: 1em;
      input{
        width: 100%;
      }
    }
    .pageChange{
      margin: 1em auto;
      width: 90%;
      padding: 0em;
      border: 2px solid;
      display: flex;
      justify-content: space-between;
      button{
        background-color: blue;
        color: #ffffff;
        border-radius: 5px;
        padding: 0.5em;
        outline: none;
      }
    }
  }

  .section3{
    border: 2px solid red;
    padding: 2em;
    padding-bottom: 3em;
    .confirm{
      width: 70%;
      margin: 2em auto;
      padding: 2em;
      border-radius: 5px;
      box-shadow: 0 0 15px #ffffff,0 0 20px#aa7f7f;
      text-align: center;
      h2{
        text-transform: capitalize;
        font-weight: bolder;
      }
    }
    .pageChange{
      margin: 1em auto;
      width: 90%;
      padding: 0em;
      display: flex;
      justify-content: space-evenly;
      button{
        background-color: blue;
        color: #ffffff;
        border-radius: 5px;
        padding: 0.5em;
        outline: none;
      }
    }
  }
}